	import {
		mapState,
		mapMutations
	} from 'vuex'
	import mInput from '../../components/m-input.vue'
	export default {
		components: {
			mInput
		},
		data() {
			return {
				// providerList: [],
				// hasProvider: false,
				account: '13211616601',
				password: '123456',
				positionTop: 0,
				// isDevtools: false,
			}
		},
		methods: {
			initPosition() {
				/**
				 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
				 * 反向使用 top 进行定位，可以避免此问题。
				 */
				this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
			},
			bindLogin() {
				/**
				 * 客户端对账号信息进行一些必要的校验。
				 */
				if (this.account.length < 11) {
					uni.showToast({
						icon: 'none',
						title: '账号最短为11 个字符'
					});
					return;
				}
				if (this.password.length < 6) {
					uni.showToast({
						icon: 'none',
						title: '密码最短为 6 个字符'
					});
					return;
				}
				/**
				 * 客户端对账号信息进行一些必要的校验。
				 */
				if(this.account==13211616601 && this.password==123456){
					uni.reLaunch({
						url:"../Setup/Setup"
					})
				}else{
					uni.showToast({
						icon: 'none',
						title: '登录失败，请检查用户名和密码'
					});
				}
			},
		},
		onReady() {
			this.initPosition();
    }
	}
